//import "./Register.css";
import React, { useState } from 'react';
import Header from '../header/Header';
import { jwtDecode } from "jwt-decode";
import { API } from '../../API';

export default function CambiarPassword() {
  const API_URL = API
  const [contraseaActual, setContraseaActual] = useState('');
  const [nuevaContrasea, setNuevaContrasea] = useState('');
  const [repetirNuevaContrasea, setRepetirNuevaContrasea] = useState('');
  const [error, setError] = useState('');
  const [successMessage, setSuccessMessage] = useState('');


  const handleSubmit = async (e) => {
    e.preventDefault();
    if (nuevaContrasea !== repetirNuevaContrasea) {
      setError('Las contraseas nuevas no coinciden.');
      return;
    }

    let correoUsuario;

    const tokenProfesor = localStorage.getItem("tokenProfesor");
    const tokenAdmin = localStorage.getItem("tokenAdmin");

    if (tokenProfesor) {
      const usuarioInfo = jwtDecode(tokenProfesor);
      correoUsuario = usuarioInfo.email; // Asegrate de que el token contenga el campo 'email'
    } else if (tokenAdmin) {
      const usuarioInfo = jwtDecode(tokenAdmin);
      correoUsuario = usuarioInfo.email; // Asegrate de que el token contenga el campo 'email'
    }

    try {
      const response = await fetch(`${API_URL}/api/cambiar-contrasena`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          correo: correoUsuario,
          contrasenaActual: contraseaActual,
          contrasenaNueva: nuevaContrasea
        })
      });

      if (!response.ok) {
        throw new Error('Error al actualizar la contrasea');
      }
      setSuccessMessage('Contrasea cambiada con xito');
      setContraseaActual('');
      setNuevaContrasea('');
      setRepetirNuevaContrasea('');
      console.log('Contrasea cambiada con xito');
    } catch (error) {
      console.error('Error al cambiar la contrasea:', error);
      setError('Error al cambiar la contrasea');
    }


  };



  return (
    <>
      <Header />
      <div className="register-container mt-5">
        <h1>Cambiar Contrasea</h1>
        <form onSubmit={handleSubmit}>

          <div className="mb-3">
            <label htmlFor="contraseaActual" className="form-label">Contrasea Actual</label>
            <input
              type="password"
              className="form-control"
              id="contraseaActual"
              value={contraseaActual}
              onChange={(e) => setContraseaActual(e.target.value)}
              required
            />
          </div>
          <div className="mb-3">
            <label htmlFor="nuevaContrasea" className="form-label">Nueva Contrasea</label>
            <input
              type="password"
              className="form-control"
              id="nuevaContrasea"
              value={nuevaContrasea}
              onChange={(e) => setNuevaContrasea(e.target.value)}
              required
            />
          </div>
          <div className="mb-3">
            <label htmlFor="repetirNuevaContrasea" className="form-label">Repetir Nueva Contrasea</label>
            <input
              type="password"
              className="form-control"
              id="repetirNuevaContrasea"
              value={repetirNuevaContrasea}
              onChange={(e) => setRepetirNuevaContrasea(e.target.value)}
              required
            />
          </div>
          {error && <div className="alert alert-danger">{error}</div>}
          {successMessage && <div className="alert alert-success">{successMessage}</div>}
          <button type="submit" className="btn btn-primary">Cambiar Contrasea</button>
        </form>
      </div>
    </>
  );
}